@import url(https://fonts.googleapis.com/css2?family=Fuzzy+Bubbles:wght@400;700&display=swap);
*{box-sizing:border-box}html{font-family:"Fuzzy bubbles", sans-serif}body,h1{margin:0}.app{background-color:rgba(27,27,29,0.31);height:100vh;padding:40px}.app__title{margin:0 auto;border:#000 solid 1px;width:660px;padding:10px;text-align:center;color:#000}.app__box{margin:0 auto;height:640px;width:660px;display:flex;justify-content:space-between;align-items:center}.app__buttons{display:flex;flex-direction:column;justify-content:space-between;height:200px}.app__button{padding:10px;width:150px;background-color:rgba(237,196,33,0.79);border:black solid 1px;box-shadow:1px 1px 10px 4px rgba(0,0,0,0.15)}

.clock{margin-bottom:40px;height:460px;width:460px;display:flex;justify-content:space-evenly;align-items:center;border:rgba(237,196,33,0.79) double 15px;border-radius:50%;box-shadow:inset 185px 10px 0 15px rgba(0,0,0,0.18)}.clock__name{width:160px;text-align:center}

